/*------------NAV------------ */
#logo-lucy {
    background-image: url(/static/media/lucyName.39b1da16.png);
    background-size: 100%;
    background-repeat: no-repeat;
    height: 3.2rem;
    width: 14.5rem;
    margin-top: 1rem;
  }
  
  .header-nav {
    width: 100%;
    font-size: 1rem;
    /*Flexbox */
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    position: fixed;
    background-color: white;
    z-index: 1;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    /* border: 1px solid black */
  }
  
  .header-nav-a {
    padding: 7px;
    border-right: 1px black solid;
    text-decoration: none;
    color: inherit;
  }
.home-container {
  width: 100%;
  margin: auto;
  /*Flexbox */
  display: flex;
  flex-flow: row wrap;
}


.menu-item {
  display: flex;
  flex-direction: column;
}

.images-menus {
  padding: 10px;
  margin: 10px;
  width: 100%;
  vertical-align: middle;
  margin: 20px;
}

.images-small {
  height: 302px;
  width: 450px;
  border-radius: 20px;
}

.images-menus-title {
  padding-bottom: 20px;
}

.images-menus-text {
  position: relative;
  font-size: 16px;
  top: -10px;
  left: -10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
  opacity: 0;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#ff8177),
    color-stop(0%, #ff867a),
    color-stop(21%, #ff8c7f),
    color-stop(52%, #f99185),
    color-stop(78%, #cf556c),
    to(#b12a5b)
  );
  background: -webkit-linear-gradient(
    left,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  background: linear-gradient(
    to right,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
}

.images-small:hover .images-menus-text {
  opacity: 0.7;
}

.images-large {
  height: 640px;
  width: 450px;
  border-radius: 20px;
}

.images-menus-hover-small {
  height: 302px;
  width: 450px;
}

.images-large:hover .images-menus-text {
  opacity: 0.7;
}

.images-high {
  height: 290px;
  width: 940px;
  border-radius: 20px;
}

.images-high:hover .images-menus-text {
  opacity: 0.7;
}

